.SideMenu {
    width: 150px;
    height: 350px;
    background: #eee;
    display: inline-block;
    vertical-align: top;
}

.SettingsContainer {
    width: 500px;
    height: 350px;
    display: inline-block;
    vertical-align: top;
}

.Table {
    width: 100%;
}

.Table tr {
    height: auto;
}

.Table tr td:first-child {
    width: 100px;
    min-width: 100px;
    max-width: 100px;
    word-break: break-all;
}
